import React from 'react';
import { AlertTriangle, CheckCircle, AlertCircle } from 'lucide-react';

interface AnalysisResult {
  id: string;
  date: string;
  severity: string;
  confidence: number;
}

interface AnalysisHistoryProps {
  history: AnalysisResult[];
}

const severityColors: Record<string, string> = {
  'No DR': 'bg-green-100 text-green-800',
  'Mild DR': 'bg-yellow-100 text-yellow-800',
  'Moderate DR': 'bg-orange-100 text-orange-800',
  'Severe DR': 'bg-red-100 text-red-800',
  'Proliferative DR': 'bg-red-200 text-red-900'
};

const CONFIDENCE_THRESHOLDS = {
  HIGH: 70,
  MODERATE: 50,
  LOW: 30
};

const getConfidenceIcon = (confidence: number) => {
  if (confidence >= CONFIDENCE_THRESHOLDS.HIGH) return <CheckCircle className="w-5 h-5 text-green-600" />;
  if (confidence >= CONFIDENCE_THRESHOLDS.MODERATE) return <AlertCircle className="w-5 h-5 text-yellow-600" />;
  return <AlertTriangle className="w-5 h-5 text-red-600" />;
};

export const AnalysisHistory: React.FC<AnalysisHistoryProps> = ({ history }) => {
  return (
    <div className="mt-8">
      <h2 className="text-xl font-semibold mb-4">Analysis History</h2>
      <div className="bg-white rounded-lg shadow overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Date</th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Severity</th>
              <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Confidence</th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {history.map((result) => (
              <tr key={result.id}>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">{result.date}</td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full ${severityColors[result.severity]}`}>
                    {result.severity}
                  </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  <div className="flex items-center">
                    {getConfidenceIcon(result.confidence)}
                    <span className="ml-2">{result.confidence.toFixed(1)}%</span>
                  </div>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

